@import url('https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700');

.App{
  overflow: hidden;
}

.WeatherOverview, .TodayCard, .OtherCard{
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  overflow: hidden;
  font-family: 'Quicksand', sans-serif;
  color: #fff;
}

.TodayCard, .OtherCard{
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.OtherCard{
  z-index: 2;
}

.OtherCard div {
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}


.City-TC{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 3.3em;
  -ms-flex-align: end;
      align-items: flex-end;
}

.Date-TC{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 1.3em;
}

.Temp-TC{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 2;
      flex-grow: 2;
  font-size: 5.3em;
  font-weight: 500;
}

.Weather-TC{
  position: absolute;
  top:50%;
  opacity: 0.2;
  font-size: 20em;
}


.TodayCard{
  position: relative;
  -ms-flex-positive: 2;
      flex-grow: 2;
  background: #3091CE;
}

.OtherCard{
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 2em;
}

.OtherCard i{
  font-size: 3em;
}


